var FS = require('fs');

module.exports = {
  /**
   * base64
   * @param uri
   * @param fileName
   * @param next
   */
  decode: function decode(uri, fileName, next) {
    var base64Data = uri.replace(/^data:.+;base64,/, "");
    var dataBuffer = new Buffer(base64Data, 'base64');
    FS.writeFile(fileName, dataBuffer, next);
  },
  decodeSync: function decodeSync(uri, fileName) {
    var base64Data = uri.replace(/^data:.+;base64,/, "");
    var dataBuffer = new Buffer(base64Data, 'base64');
    FS.writeFileSync(fileName, dataBuffer);
  },

  /**
   * base64
   * @param filename
   * @param next
   */
  encode: function encode(filename, next) {
    FS.readFile(filename, function (err, data) {
      if (err) {
        next(err);
      } else {
        next(null, data.toString('base64'));
      }
    });
  },
  encodeSync: function encodeSync(filename, next) {
    var data = FS.readFileSync(filename);
    return data.toString('base64');
  }
};